<template>
  <div>
    <mavon-editor
      ref="editor"
      :boxShadow="boxShadow"
      :subfield="false"
      :toolbars="toolbars"
      :value="content"
      :placeholder="placeholder"
      @imgAdd="imgAdd"
      @change="handleValueChange"
    />
  </div>
</template>

<script>
import 'mavon-editor/dist/css/index.css'
const mavonEditor = require('mavon-editor')
export default {
  components: {
    mavonEditor: mavonEditor.mavonEditor
  },
  props: {
    // value: {
    //   type: String,
    //   default: ""
    // },
    placeholder: {
      type: String,
      default: ''
    }
  },
  data () {
    const data = this.data || ''
    return {
      content: data,
      boxShadow: false,
      toolbars: {
        bold: true, // 
        italic: true, // 
        header: true, // 
        underline: true, // 
        strikethrough: true, // 
        mark: true, // 
        superscript: true, // 
        subscript: true, // 
        quote: true, // 
        ol: true, // 
        ul: true, // 
        link: true, // 
        imagelink: true, // 
        code: true, // code
        table: true, // 
        fullscreen: true, // 
        readmodel: false, // 
        htmlcode: true, // html
        help: true, // 
        undo: true, // 
        redo: true, // 
        trash: true, // 
        save: true, // eventssave
        navigation: true, // 
        alignleft: true, // 
        aligncenter: true, // 
        alignright: true, // 
        subfield: true, // 
        preview: true // 
      }
    }
  },
  watch: {
    value (val = '') {
      this.content = val
    }
  },
  methods: {
    handleValueChange (val) {
      this.triggerChange(val)
    },
    triggerChange (val) {
      console.log(this.$data)
      this.$emit('change', val)
    },
    imgAdd (pos, $file) {
      var formdata = new FormData()
      formdata.append('file', $file)
      // upload(formdata).then(res => {
      //   const url = res.data.path;
      //   this.$refs.editor.$img2Url(pos, url);
      // });
    }
  }
}
</script>

